import React, { useState } from "react";

const App = () => {
  const [name, setname] = useState();
  const inputEvent = (event) => {
    console.log(event.target.value);
    setname(event.target.value);
  };
  return (
    <>
      <div>
        <h1>Hello {name} </h1>
        <input
          type="text"
          placeholder="Enter your name"
          onChange={inputEvent}
        />
        <button>click me</button>
      </div>
    </>
  );
};

export default App;
